import { Injectable } from '@angular/core'
import { HttpApiService } from '../http-api/http-api.service'
import { HttpHeaders } from '@angular/common/http'
import { object_to_url } from './../../shared/functions/object-to-urlparams'

@Injectable()
export class sendSelectPostService {
  public loginFlag
  constructor(private api: HttpApiService) {}

  async send(data) {
    const fields_response: any = await this.api.post('createArchive', data)
    if (fields_response.success == true) {
      const a = document.createElement('a')
      a.href = 'http://185.195.26.110:8080/example.zip'

      document.body.appendChild(a)
      a.click()
    }
  }
}
